.wrapper {
    display: flex;

    .content {    
        display: flex;
        max-width: 1355px;
        margin: 0 auto;
        width: 100%;
        padding: 0 55px;
        box-sizing: border-box;
        flex-direction: column;
    }
}

.box {
    display: flex;
    padding: 25px 0;
    width: 100%;
    border-top: 1px solid #151F2A;    

    .icons {
        display: flex;
        font-size: 0;

        &.right {
            margin-left: auto;
        }

        svg {
            display: inline-block;
            margin-right: 20px;
            height: 28px;
        }

        svg:last-child {
            margin-right: 0;
        }
    }

    .links {
        display: flex;

        .list {
            display: flex;
            flex-direction: column;
            padding-right: 85px;
            
            &:last-child {
                padding-right: 0;
            }

            .title {
                display: flex;
                font-weight: 600;
                font-size: 14px;
                line-height: 19px;
                color: #FFFFFF;
                padding-bottom: 5px;
            }

            .link {
                margin-top: 10px;
                text-decoration: none;
                display: flex;
                font-weight: 400;
                font-size: 14px;
                line-height: 19px;
                color: #6A7895;
            }
            
        }
    }

    .notice {
        display: flex;
        margin: auto 0 auto auto;

        svg:nth-child(1) {
            margin-right: 45px;
        }
    }
}

.copyright {
    display: flex;
    padding: 30px 0;
    width: 100%;
    border-top: 1px solid #151F2A; 

    .information {
        display: flex;
        flex-direction: column;

        svg {
            width: 109px;
            height: 29px;
            margin-bottom: 10px;
        }

        .text {
            width: 521px;
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            color: #8394B6;
        }
    }

    .right {
        display: flex;
        margin: auto 0 auto auto;
        align-items: center;
        
        .email {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            color: #8394B6;
            margin-left: 5px;
        }

        .links {
            display: flex;
            margin-left: 40px;
            margin-right: 40px;

            a {
                display: flex;
                margin-left: 20px;

                &:hover path {
                    fill: rgba(106, 120, 149, 0.8509803922);
                }
            }
        }
    }
}
