.wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    
    .content {
        display: flex;
        flex: 1 0 auto;
        height: 100%;
        flex-direction: column;
    }
}