.wrapper {    
    display: flex;
    height: 100%;
    align-items: start;

    .tag {
        margin-top: 18px;
        background: #222B3E;
        border-radius: 5px 0px 0px 5px;
        padding: 10px 20px;
        cursor: pointer;
    }

    .chat {
        display: flex;
        height: 100%;
        width: 359px;
        z-index: 2;
        background: #1a212f;

        .head {
            padding: 20px 15px;
            position: relative;
            box-sizing: border-box;
            width: 100%;
            height: 80px;
            background: #222B3E;
            display: flex;
        
            &::before {
                content: "";
                display: block;
                position: absolute;
                background: linear-gradient(180deg, rgba(14, 18, 27, 0.8) 0%, rgba(14, 18, 27, 0) 100%);
                width: 100%;
                height: 50px;
                bottom: 0;
                left: 0;
                margin-bottom: -50px;
            }
        }
    }
}