$line_style: 1px solid #1f283a;

.wrapper {
    display: flex;
    height: 100%;
    flex-direction: column;
    width: 206px;
    z-index: 2;
    background: #1a212f;
    transition: width 0.3s ease;

    .hide {
        display: flex;
        padding: 27.5px 0 27.5px 25px;
        cursor: pointer;
        white-space: nowrap;
        overflow: hidden;

        span {
            margin: 2.5px 0 0 15px;
            display: inline-block;
            vertical-align: top;
            font-weight: 600;
            font-size: 14px;
            line-height: 19px;
            color: #6a7895;
        }

        &:hover {
            path {
                fill: #6f7f9d;
            }

            span {
                color: #6f7f9d;
            }
        }
    }

    .home {
        display: flex;
        border-top: $line_style;
        border-bottom: $line_style;
        padding: 11px 0;
    }

    .scrollBox {
        width: 100%;
        display: flex;
        flex: 1 0 auto;
    }

    .link {
        display: block;
        width: 100%;
        padding: 6px 1px 6px 24px;
        border-left: 1px solid transparent;
        box-sizing: border-box;
        white-space: nowrap;
        overflow: hidden;
        transition: border 0.7s ease;
    
        svg {
            width: 23px;
            height: 23px;
        }
    
        span {
            margin: 2.5px 0 0 15px;
            display: inline-block;
            vertical-align: top;
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            color: #9ba5ba;
            transition: padding 0.4s ease;
        }
    
        &:hover {
            border-color: #8394b6;
    
            path {
                fill: #8394b6;
            }
    
            span {
                font-weight: 500;
                color: #8394b6;
                padding-left: 3px;
            }
        }
        
        &.isActive {
            cursor: default !important;
            border-color: #776bf8;
    
            path {
                fill: #776bf8 !important;
            }
    
            span {
                font-weight: 600 !important;
                color: #ffffff !important;
                padding-left: 0 !important;
            }
        }
    }

    .links {
        padding: 16px 0;

        .link {
            margin-bottom: 12px;
        }
        
        .link:last-child{
            margin-bottom: 0;
        }

        &.line {
            border-top: $line_style;
        }
    }

    .buttons {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 15px;
        border-top: $line_style;
        box-sizing: border-box;

        .button {
            cursor: pointer;
            width: 100%;
            background: #222b3e;
            border-radius: 3px;
            margin: 0 auto;
            box-sizing: border-box;
            padding: 15px;
            white-space: nowrap;
            overflow: hidden;
        
            span {
                padding: 2px 0 0 10px;
                display: inline-block;
                vertical-align: top;
                font-weight: 600;
                font-size: 14px;
                line-height: 19px;
                color: #6a7895;
            }
        
            svg {
                width: 23px;
                height: 23px;
            }
        
            &:hover {
                background: linear-gradient(180deg, #222b3e 0%, #273148 100%);
        
                span {
                    color: #8394b6;
                }
            }
        
            &:first-child {
                margin-bottom: 10px;
            }
        }
    }

    &.isHide {
        width: 76px;
        transition: width 0.1s ease;

        span {
            display: none !important;
        }

        .buttons {
            padding: 15px 11px;
        }
        
        .link {
             padding-left: 0;
        }

        svg {
            display: block;
            margin: 0 auto;
        }

        .hide {
            padding-left: 0;
            margin: 0 auto;

            &:hover {
                path {
                    fill: white !important;
                }
            }
        }
    }
}
